/* SpanX based grid elements */
.row:before,
.row:after {
 display: table;
 line-height: 0;
 content: "";
}

.row {
 margin: 0 -1em;
}

.row:after {
 clear: both;
}

[class*="span"] {
 display: block;
 float: left;
 padding: 0 1em;
 width: 100%;
 margin: 0 0 1.8em 0;
}

[class*="span"] img {
 height: auto;
 padding: 0;
}

.span1 {width: calc(1/12*100%);}
.span2 {width: calc(2/12*100%);}
.span3 {width: calc(3/12*100%);}
.span4 {width: calc(4/12*100%);}
.span5 {width: calc(5/12*100%);}
.span6 {width: calc(6/12*100%);}
.span7 {width: calc(7/12*100%);}
.span8 {width: calc(8/12*100%);}
.span9 {width: calc(9/12*100%);}
.span10 {width: calc(10/12*100%);}
.span11 {width: calc(11/12*100%);}
.span12 {width: calc(12/12*100%);}


.offset1{margin-left:8.3333%}
.offset2{margin-left:16.666667%}
.offset3{margin-left:25%}
.offset4{margin-left:33.333%}
.offset5{margin-left:41.6667%}
.offset6{margin-left:50%}
.offset7{margin-left:58.3333%}
.offset8{margin-left:66.6667%}
.offset9{margin-left:75%}
.offset10{margin-left:83.3333%}
.offset11{margin-left:91.6667%}
.offset12{margin-left:100%}

/* END -- SpanX based grid elements */

