/* TOP BAR NAVIGATION */

/* temp to remove margins */

#navigation {
	transform: translate(0, -1000px);
	transition: transform 5s, opacity 7s;
	opacity: 0;
	display: none;
	color: white;
	margin: 0;
	padding-top: $xl-space;
}
/* TOP BAR NAVIGATION */


.nav-full {
	margin-bottom: 0;
	padding-bottom: 0;
	ul {
		font-weight: 700;
		list-style-type: none;
		height: 100%;
		li {
			display: block;
			float: left;
			transition-duration: 0.2s;
			position: relative;
			padding: 1em;
			background: #222;
			color: lightgrey;
			margin: 0!important;
		}

		li:hover {
			cursor: pointer;
			background: lightgrey;
		}

		li a {
			float: left;
			color: #f7f7f7;
		}

		li:hover a {
			color: #222;
		}
		li ul {
			visibility: hidden;
			opacity: 0;
			position: absolute;
			transition: all 0.5s ease;
			left: 0;
			display: none;
			top: 3.5em;
			z-index: 99;

		}

		li ul:hover,
		li:hover > ul {
			visibility: visible;
			opacity: 1;
			display: block;
		}

		li ul li {
			clear: both;
			width: 10em;
			padding: 1em;
			background: lightgrey;
			color: #222;
			margin: 0!important;
			text-align: left;
		}
		li ul li a {
			color: #222;
			margin: 0!important;
			width: 100%;
}

		li ul li:hover {
			background: #222;
			color: lightgrey;
		}
		li ul li:hover a {
			color: lightgrey;
		}

	}


	.active,
	.active a {
		background: lightgrey;
	}


	.material-icons {
		padding: 0;
		margin: 0;
		font-size: inherit;
	}
}






/* END __ TOP BAR NAVIGATION */






ul.nav-expanded {

 display: none;
 clear: both;
