 font-size: 14px;
 }

	.centered {
 max-width: 100%;
 max-height: 30em;
 text-align: center;
 }

 [class*='offset'] {
 margin-left: 0;
 }

 .content {
 padding: 0 $l-space;
 }

 .span3 {
 width: 50%;
 }



 .span4 {
 width: 50%;
