 nav {
 color: $bodycolor;
 position: fixed;
 top: 0;
 right: 0;
 z-index: 1000;
 cursor: pointer;
 transform: translate(0,0);
 transition: transform 0.4s, height 0.3s, background 0.4s;
 animation: firstFadeInAndDrop 0.5s;
 }


 nav a {
 text-decoration: none;
 border-bottom: 0;
 }
 /* Animate Drop Down of Menu */
 @keyframes firstFadeInAndDrop {
 0% {
 transform: translate(0,-54px);
 }

 100% {
 transform: translate(0,0);
 }
 }
 /* increases menu width & height NAV BACK COLOUR */
 nav.expanded {
 width: 100%;
 height: 100%;
 cursor: default;
 background: $bodycolor;
 opacity: 0.98;
 z-index: 997;
 overflow: scroll;
 }

 nav.expanded {
 body {
 filter:blur(20px)
 }
 }
 /* positions navigation content */


 /* shows navigation */
 nav.expanded #navigation {
 transform: translate(0, 0);
 opacity: 1;
 display: block;
 margin: auto;
 z-index: 998;
 position: relative;
 width: 100%;
 }
 /* Bar Transition to X */
 #nav-container {
 position: fixed;
 top: $m-space;
 right: $m-space;
 margin: 0;
 z-index: 999;
 }

 .toggle-icon {
 position: relative;
 display: block;
 width: 100%;
 margin: 0;
 cursor: pointer;
 transition: margin 600ms;
 }
 /* Positiioning Bars */
 .toggle-icon .bar {
 display: block;
 width: 3em;
 height: 0.3em;
 margin-bottom: 0.6em;
 transition-duration: 600ms;
 transition-property: transform, margin, border-right-color, box-shadow;
 background: white;
 }

 .toggle-icon .bar:nth-of-type(3) {
 margin-bottom: 0;
 }

 .toggle-icon .bar:nth-of-type(2) {
 -webkit-transition: opacity 300ms;
 -moz-transition: opacity 300ms;
 transition: opacity 300ms;
 }

 .pushed .bar {
 background: white;
 }

 .pushed .bar:nth-of-type(2) {
 opacity: 0;
 }

 .pushed .bar:nth-of-type(1) {
 transform: translateY(.9em) rotate(-135deg);
 }

 .pushed .bar:nth-of-type(3) {
 transform: translateY(-.9em) rotate(-45deg);
 }
 /* Shrink Title on scroll */
 .shrink {}

 .no_scroll {
 overflow: hidden;
 }
 /* End source navigation */
 .nav-full ul {
 display: none;
 }

 ul.nav-expanded {
 display: block;
 clear: both;

 li {
 display: block;
 clear: both;
 font-size: 1.5em;
 margin-bottom: $m-space;
 }

 li a:hover {
 color: $accent-color;
 text-decoration: none;
 }
 }
}


